<template>
  <div class="row">
    <div class="col-lg-8 offset-lg-2 col-md-10 offset-md-1 col-sm-12 offset-sm-0" v-if="categories">
      <div class="main-add-topics--section add-global-mrgn-pddng--kit">
        <h5 class="global-first-header-page--elem">Edit topic</h5>
        <div class="main-add-topics-form--area global-texteditor-style--area">
          <div class="main-add-topic-fields--elem">
            <label for="title">What's your topic?</label>
            <input type="text" name="title" id="title" placeholder="Head Title.." v-model="title">

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.title"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.title.message }}</span>
          </div>
          <div class="main-add-topic-fields--elem">
            <label for="body">Describe your topic!</label>
            <froala :tag="'textarea'" id="body" :config="config" v-model="body"></froala>

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.body"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.body.message }}</span>
          </div>
          <div class="main-add-topic-fields--elem global-select-style--area">
            <label for="body">Which category you choose?</label>
            <v-select v-model="categoryId" placeholder="Select category.." label="name" :options="categories"></v-select>

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.categoryId"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.categoryId.message }}</span>
          </div>

          <button class="global-btn-elements blue-skies-gradient btn--target" @click="updateTopic">Save Now!</button>
        </div>
      </div>
    </div>

    <transition
      name="custom-classes-transition"
      enter-active-class="animated bounceInUp"
      leave-active-class="animated bounceOutDown"
    >
      <div class="fixed--layout" v-if="saveDetected">
        <div class="main-alert-detected--area save--kit">
          <i class="las la-check-circle"></i> Updated With Success!
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import router from './../../../routes/routes'


import emit from "./../../../utils/emit";
import { configFloaraWYSIWYG, getAllCategrories, getDecodedHtmlText } from "./../../../utils/globalHandlers";
import { _getUserId } from "./../../../utils/auth";
export default {
    props: ['user'],
    data() {
      return {
        id: this.$route.params.id,
        title: "",
        body: "",
        categoryId: "",
        // userId: this.user._id,
        config: configFloaraWYSIWYG('Description..'),

        categories: null,

        saveDetected: false,

        errorDetected: false,
        errorData: null
      }
    },
    created() {
      this.getTopicById()
      this.getCategories()
    },
    methods: {

      async getTopicById(){
        try {
          let res = await axios({
            method: 'GET',
            url: `/api/v1/topics/${this.id}`
          })

          //res.data.data.data.userId._id
          this.checkIsBelong(res.data.data.data.userId._id)

          this.title = res.data.data.data.title
          this.body = getDecodedHtmlText(res.data.data.data.body)
          this.categoryId = res.data.data.data.categoryId
          
        } catch (err) {
          console.log(err.response.data);
          console.log(err);
        }
      },

      async checkIsBelong(id){
        let userId = await _getUserId()
        let creatorId = id

        let isBelong = creatorId === userId

        if(!isBelong) return this.$router.push('/error');
      },
      
      async updateTopic(){
        try {
          let res = await axios({
              method: "PATCH",
              url: `/api/v1/topics/${this.id}`,
              data: {
                  title: this.title, 
                  body: this.body,
                  categoryId: this.categoryId._id
              }
          });

          if(res.data.status === "success"){
            this.saveDetected = true
            setTimeout(() => { this.saveDetected = !this.saveDetected }, 2500)
          }
        } catch (err) {
          console.log(err.response.data);
          this.errorDetected = true;
          this.errorData = err.response;
        }
      },


      async getCategories(){
        this.categories = await getAllCategrories()
      }
    },
}
</script>

