<template>
  <div class="col-lg-10 offset-lg-1 col-md-12 offset-md-0 col-sm-12 offset-sm-0">
    <div class="add-global-mrgn-pddng--kit">
      <h5 class="global-first-header-page--elem">My Topics List <router-link to="/dashboard/topics/add"><i class="las la-plus"></i> Add New Topic</router-link></h5>

      <div class="main-content-loading--area" v-if="!contentLoaded">
        <div class="main-fake-search-bar--elem gradient"></div>

        <div>
            <div class="main-fake-list-item gradient"></div>
            <div class="main-fake-list-item gradient"></div>
            <div class="main-fake-list-item gradient"></div>
            <div class="main-fake-list-item gradient"></div>
            <div class="main-fake-list-item gradient"></div>
        </div>
      </div>

      <div class="main-topics-mylist--area" v-else>
        <div class="main-search-bar--area">
          <input type="text" placeholder="Find a topic.." v-model="search" @keyup="checkTopicsLength()">
        </div>

        <div class="main-topics-list--area">
          <div v-if="getTopicsWithSplicingAndFiltring.length">
            <div  
              class="main-topics-item--elem" 
              v-for="topic in getTopicsWithSplicingAndFiltring" 
              :key="topic.id" 
              
              v-show="!checkIfDelete(topic.id)">

              <div class="main-body-table--area globla-table--area">
                <div class="main-full-column--elem main-custom-right-paddin-50--kit">
                    <router-link :to="`/dashboard/topics/edit/${topic.id}`">
                      <strong>{{ topic.title }}</strong>
                      <i class="las la-pencil-alt"></i>
                    </router-link>

                    <div class="main-intro-text--elem" v-html="getHtml(topic.intro)"></div>
                </div>
                <div class="main-delete-action--area">
                  <div 
                    class="main-delete-btn--elem" 
                    :class="{ blockClick: !deleteBtnActive }"
                    @click="deleteTopic(topic.id)">
                    <i class="las la-trash-alt"></i>
                  </div>
                </div>
              </div>

            </div>
          </div>

          <div class="no-content-msg--area" v-else>No Topics Founded!</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
/* eslint-disable */
import axios from 'axios';

import emit from "./../../../utils/emit";

import { configFloaraWYSIWYG, getAllCategrories, getDecodedHtmlText } from "./../../../utils/globalHandlers";
import { _getUserId } from "./../../../utils/auth";

export default {
  data() {
    return {
      topicsLength: 0,
      topics: [],
      search: '',

      deleteBtnActive: true,
      deletedTopicsList: [],

      contentLoaded: false
    }
  },
  created() {
    this.getTopicsBelongToUser()
  },
  methods: {
    async getTopicsBelongToUser(){
      try {
        let userId = await _getUserId()

        let res = await axios({
          method: 'GET',
          url: `/api/v1/topics?userId=${userId}`//`/api/v1/topics/belongToUser/${userId}`
        })

        this.topicsLength = res.data.data.data.length
        this.topics = res.data.data.data
        this.contentLoaded = true
        console.log(this.topics) 
      } catch (err) {
        console.log(err)
      }
    },

    async deleteTopic(id){
      if(this.deleteBtnActive === true){
        try {
          this.deleteBtnActive = false
          this.deletedTopicsList.push(id)

          await axios({
            method: 'DELETE',
            url: `/api/v1/topics/${id}`
          });

          this.getTopicsBelongToUser();
          emit.$emit('deletedAlert');
          this.deleteBtnActive = true
        } catch (err) {
          console.log(err)
        }
      }
    },
    checkIfDelete(id){
      return (this.deletedTopicsList.includes(id)) ? true : false
    },

    getHtml(h){
      return getDecodedHtmlText(h)
    },
    checkTopicsLength(){
      if(this.search != ''){
        this.topicsLength = this.topics.filter(el => { return el.title.toLowerCase().includes(this.search.toLowerCase()) }).length
      }else{
        this.topicsLength = this.topics.length
      }
    }
  },
  computed: {
    getTopicsWithSplicingAndFiltring() {
      return this.topics.filter(el => {
        return el.title.toLowerCase().includes(this.search.toLowerCase())
      }) //.splice(0, this.defaultSplice)
    }
  },
}
</script>

