/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import { isLoggedIn } from './../utils/auth'

// import Home from './../views/Home.vue'

// Pages components
import aboutPage from './../views/pages/about.vue'


// Auth components
import loginPage from './../views/auth/login-page.vue'
import registerPage from './../views/auth/register-page.vue'

// Topics components
import allTopicsPgae from './../views/topics/all-topics-page.vue'
import singleTopicPage from './../views/topics/single-topic-page.vue'

// Dashboard components
import settingProfilePage from './../views/dashboard/setting-profile-page.vue'
import settingPasswordPage from './../views/dashboard/setting-password-page.vue'
import myTopicsPage from './../views/dashboard/topics-crud/my-topics-page.vue'
import editTopicPage from './../views/dashboard/topics-crud/edit-topic-page.vue'
import createTopicPage from './../views/dashboard/topics-crud/create-topic-page.vue'


// Errors components
import e404Pgae from './../views/404-error-page.vue'


Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    // Pages Routes
    { path: '/about', name: 'about', component: aboutPage, meta: { allowAnonymous: true } },

    // Auth Routes:
    { path: '/login', name: 'login', component: loginPage, meta: { allowAnonymous: true } },
    { path: '/register', name: 'register', component: registerPage, meta: { allowAnonymous: true } },

    //
    { path: '/', name: 'topics-page', component: allTopicsPgae, meta: { allowAnonymous: true } },
    { path: '/topics/:id', name: 'single-topic-page', component: singleTopicPage, props: true, meta: { allowAnonymous: true } },

    // Dashboard Routes
    { path: '/setting/profile', component: settingProfilePage }, // protected
    { path: '/setting/password', component: settingPasswordPage, }, // protected
    { path: '/dashboard/topics/mylist', component: myTopicsPage, }, // protected
    { path: '/dashboard/topics/add', name: 'create-topic-page', component: createTopicPage }, // protected
    { path: '/dashboard/topics/edit/:id', component: editTopicPage, props: true, }, // protected

    // 404 & Errors Routes 
    { path: '/error', name: '404-page', component: e404Pgae, meta: { allowAnonymous: true } },

    // Redirect to 404 path "if nothing found!"
    { path: '*', redirect: '/error'  },
  ]
})

router.beforeEach((to, from, next) => {
  if ((to.name == 'login' && isLoggedIn()) || (to.name == 'register' && isLoggedIn())) { next({ path: '/' }) }
  else if (!to.meta.allowAnonymous && !isLoggedIn()) { next({ path: '/login'}) }
  else { next() }  
})

export default router